.App {
  min-width: 1000px;
}

body {
  font-family: "Open Sans", sans-serif;
  background-color: #f1f3f6;
}

*:focus {
  outline: none;
}

button:focus {
  outline: 0;
}

.playbutton {
  opacity: 0;
  transition: 0.3s;
  transition: var(--transtime);
}

/* .playlistcover {
  border: 0.5rem solid;
  background-color: black;
  border-radius: 8%;
} */

.playlistTitle {
  position: absolute;
  top: 80%;
  left: 0%;
  opacity: 0;
  transition: 0.3s;
  transition: var(--transtime);
}

.playlistcover {
  border: 0rem solid;
  background-color: none;
  border-radius: 8%;
  transition: 0.3s;
  transition: var(--transtime);
  overflow: hidden;
}

.TitleBackground {
  position: absolute;
  width: 100%;
  top: 75%;
  height: 35%;
  background: linear-gradient(transparent, #000);
  opacity: 0;
  transition: 0.3s;
  transition: var(--transtime);
}

.playlistcover:hover .TitleBackground {
  opacity: 1;
}

.playlistcover:hover .playbutton {
  opacity: 1;
}

/* .playlistcover:hover {
  border: 0.5rem solid;
  border-radius: 8%;
} */

.playlistcover:hover .playlistTitle {
  /* top: 80%;
  left: 3%; */
  left: 5%;
  opacity: 1;
}

/** custome player slider **/

/*https://css-tricks.com/styling-cross-browser-compatible-range-inputs-css/*/

input[type="range"] {
  -webkit-appearance: none;
  /* width: 100%; */
  /* background: transparent; */
}

input[type="range"]:focus {
  outline: none;
}

input[type="range"]::-ms-track {
  width: 100%;
  cursor: pointer;
  background: transparent;
  border-color: transparent;
  color: transparent;
}

/** styling thumb for webkit/blink **/

input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;
  /* border: 1px solid #000; */
  height: 13px;
  width: 13px;
  border-radius: 50%;
  background: #181818;
  cursor: pointer;
  margin-top: -3px;
  /* box-shadow: 1px 1px 1px #000, 0px 0px 1px #0d0d0d; */
}

/* styling thumb for firefox */

input[type="range"]::-moz-range-thumb {
  height: 10px;
  width: 10px;
  border-radius: 50%;
  background: #181818;
  cursor: pointer;
}

/* Styling the Track */

/* FOR WEBKIT */

input[type="range"]::-webkit-slider-runnable-track {
  /* width: 100%; */
  height: 6px;
  cursor: pointer;
  /* box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d; */
  background: #9e9e9e;
  border-radius: 5px;
  /* border: 0.2px solid #010101; */
}

input[type="range"]:focus::-webkit-slider-runnable-track {
  background: #2bc962;
}

/*FOR FIREFOX*/

input[type="range"]::-moz-range-track {
  height: 6px;
  cursor: pointer;
  background: #9e9e9e;
  border-radius: 5px;
}

